<template>
  <div class="introduce-container vip_container">
    <introduce-page-title :index="1"></introduce-page-title>
    <div class="content-box">
      <div class="vip_introduce">
        <h2>选择适合你扩容套餐</h2>
        <p>XPE 智能办公平台容易上手，功能强大，我们提供项目15个，征信次数50次的基础款套餐，以及专业版的项目及征信阶梯付费套餐，满足不同规模的专业PE企业使用，可以在下文选择适合贵公司的版本。项目尽调数据收集与管理，对接企业工商数据，帮助了解企业概况，定价合理，可咨询购买。</p>
      </div>
      <div class="vip_contact">
        <h2>具体详情请联系我们进行咨询</h2>
        <div class="vip_contact_phone"><img src="../../images/introduce_iconphone.png" alt="">{{phone}}</div>
      </div>
      <div class="package_list">
        <div class="package_item" @mouseenter="changePackage(index)" :class="{'current':index==currentpackage}" v-for="(item,index) in packageList" :key="index">
          <h2 class="package_title">{{item.name}}</h2>
          <p class="package_smalltitle">{{item.desc}}</p>
          <p class="package_minititle">{{item.desc1}}</p>
          <router-link :to="{name:'login'}" v-if="item.type==1" class="package_btn">
            <div class="package_btn">{{item.btn}}</div>
          </router-link>
          <div v-if="item.type==2" class="package_btn">
            <span class="noDiscount" v-if="item.discount!=1&&item.discountList.length!=0">{{setNumber(item.btn)}}</span>
            <span class="haveDiscount">¥{{setNumber(item.btn,item.discount,item.discountList)}}</span> 
          </div>
          <div class="package_vipfuncbox">
            <div class="package_vipitem_title">{{item.func.name}}</div>
            <div class="el_radio_group_box" v-if="item.type==2&&item.discountList!=0">
              <el-radio-group v-model="item.discount" >
                <el-radio :label="discountObj.value" v-for="(discountObj,index) of item.discountList" :key="index">{{discountObj.key}} <img src="../../images/icon_tj.png"  class="icon_tj" alt="" v-if="discountObj.isTj==true"> </el-radio> 
              </el-radio-group>
            </div>
            
            <div class="package_vipfunclist" v-for="(item1,index1) in item.func.list" :key="index1">
              <div class="package_vipfuncitem">
                <span v-if="item.type==1" class="iconfont icon-zhengque"></span>
                <span @click="changeFunc(index,index1,item1)" v-if="item.type==2&&item.checked!=index1" class="iconfont icon-danxuanweixuanzhong"></span>
                <span v-if="item.type==2&&item.checked==index1" class="iconfont icon-xuanzhong"></span>
                <div class="package_vipfunc_text">{{item1.name}}</div>
                <div class="package_vipfunc_span" v-if="item.type==2">{{setNumber(item1.btn)}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <introduce-page-bottom></introduce-page-bottom>
      <introduce-page-bottom1></introduce-page-bottom1> 
    </div>
  </div>
</template>
<script>
import { payBillCombo } from "../../config/api";
import introducePageTitle from "./introducePageTitle.vue"; //顶部流程模块
import introducePageBottom from "./introducePageBottom.vue"; //顶部流程模块
import introducePageBottom1 from "./introducePageBottom1.vue"; //顶部流程模块
import { numFormat } from "../../config/mUtils";
export default {
  data() {
    return {
      radio2: 3,
      currentpackage: 0,
      phone:'',
      packageList: [
        {
          type: 1,
          name: "基础款",
          desc: "加入团队即可享有",
          desc1: " ",
          totalPrice: "",
          btn: "注  册",
          func: {
            name: "免费功能：",
            list: [
              {
                name: "可创建项目15个",
                btn: ""
              },
              {
                name: "征信50次",
                btn: ""
              }
            ]
          }
        },
        {
          type: 2,
          name: "项目套餐",
          desc: "增加管理项目数量",
          desc1: "(按年支付)",
          totalPrice: "",
          btn: "50000", //默认价格
          checked: 0,
          discount: 0.6, //推荐的折扣
          discountList: [],
          func: {
            name: "包含基础款所有功能，以及以下可选功能：",
            list: []
          }
        },
        {
          type: 2,
          name: "征信套餐",
          desc: "目标公司负面、舆情随时掌握",
          desc1: "(按年支付)",
          totalPrice: "",
          btn: "2000",
          checked: 0,
          discount: 1,
          discountList: [],
          func: {
            name: "包含基础款所有功能，以及以下可选功能：",
            list: []
          }
        }
      ]
    };
  },
  components: { introducePageTitle, introducePageBottom, introducePageBottom1 },
  created() {
    // console.log(numFormat(1000))
    this.getPriceInfo();
  },
  mounted() {},
  computed: {},
  methods: {
    changeFunc(index, index1, item1) {
      this.packageList[index].checked = index1;
      this.packageList[index].btn = item1.btn;
    },
    changePackage(index) {
      this.currentpackage = index;
    },
    setNumber(num, discount,discountList) {
      let rates = 0
      if(discountList!=undefined){
        // console.log(discountList);
        discountList.forEach(element => {
          if(element.value==discount){
            rates = element.rates;
          }
        });
      }
      return discount != undefined ? numFormat(num * discount-rates) : numFormat(num);
    },
    getPriceInfo() {
      payBillCombo().then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.phone = data[0].tel;
          data.forEach(element => {
            let newList = [];
            let newDiscountInfo = [];
            if (element.list != undefined) {
              element.list.forEach((item, index) => {
                let newObj = {
                  name: item.name,
                  btn: item.price
                };
                newList.push(newObj);
              });
            }

            if (element.discountInfo != undefined) {
              element.discountInfo.forEach((item, index) => {
                let newObj = {
                  value: item.discount / 10,
                  key:
                    item.discount == 10
                      ? item.period + "年"
                      : item.period + "年(" + item.discount + "折)",
                  rates: item.rates,
                  
                };
                if(item.period==3){
                  newObj.isTj = true;
                }
                newDiscountInfo.push(newObj);
              });
            }

            if(element.type==1){
              this.packageList[1].func.list = newList;
              this.packageList[1].discountList = newDiscountInfo;
            }else if(element.type==2){
              this.packageList[2].func.list = newList;
              this.packageList[2].discountList = newDiscountInfo;
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.introduce-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  overflow: auto;
}

.vip_container {
  padding-top: 60px;
  .content-box {
    width: 100%;
    min-width: 1200px;
  }
  .vip_introduce {
    text-align: center;
    width: 720px;
    margin: 100px auto 0px;
    & > h2 {
      font-size: 42px;
      color: #282828;
      margin-bottom: 30px;
      line-height: 1em;
      font-weight: 300;
    }
    & > p {
      font-size: 14px;
      color: #35383a;
      line-height: 24px;
    }
  }
  .vip_contact {
    margin-top: 32px;
    text-align: center;
    & > h2 {
      font-size: 12px;
      color: #1787fb;
    }
    .vip_contact_phone {
      margin-top: 5px;
      font-size: 20px;
      color: #1787fb;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 20px;
        height: 16px;
        margin-right: 10px;
      }
    }
  }
  .package_list {
    display: flex;
    justify-content: center;
    margin: 50px 0px;
  }
  .package_item {
    width: 350px;
    margin: 0px 25px;
  }
  .package_title {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #282828;
    line-height: 1em;
  }
  .package_smalltitle,
  .package_minititle {
    line-height: 1em;
    height: 14px;
    font-size: 14px;
    margin-top: 8px;
    color: #282828;
    text-align: center;
  }
  .package_btn {
    width: 100%;
    height: 60px;
    cursor: pointer;
    background: #89c5f9;
    text-align: center;
    line-height: 60px;
    border-radius: 30px;
    margin-top: 14px;
    color: #fff;
    font-size: 24px;
    .noDiscount {
      color: #fff;
      font-size: 24px;
      text-decoration: line-through;
      opacity: 0.5;
      margin-right: 15px;
      letter-spacing: 1px;
    }
    .haveDiscount {
      color: #fff;
      font-size: 24px;
      letter-spacing: 1px;
    }
  }
  .package_item.current {
    .package_title {
      transition: all 0.3s;
      color: #1787fb;
    }
    .package_smalltitle,
    .package_minititle {
      transition: all 0.3s;
      color: #1787fb;
    }
    .package_btn {
      transition: all 0.3s;
      background: url("../../images/introduce_vipprice_btnbg.png") no-repeat;
    }
  }
  .package_vipfuncbox {
    padding: 0px 30px;
    margin-top: 30px;
  }
  .package_vipitem_title {
    font-size: 14px;
    color: #a0a4aa;
  }
  .package_vipitem_title1 {
    text-align: center;
  }
  .el_radio_group_box {
    display: flex;
    // justify-content:center;
    margin-top: 30px;
    width: calc(100% + 30px);
    .icon_tj {
      width: 22px;
      height: 12px;
      position: relative;
      top: 2px;
    }
  }
  .package_vipfunclist {
    margin-top: 30px;
  }
  .package_vipfuncitem {
    display: flex;
    margin-bottom: 28px;
    align-items: center;
    .iconfont {
      font-size: 20px;
      margin-right: 10px;
      cursor: pointer;
    }
    .icon-xuanzhong,
    .icon-zhengque {
      color: #3c98e8;
    }
    .icon-danxuanweixuanzhong {
      color: #e4e4e4;
    }
    .package_vipfunc_img {
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      margin-right: 5px;
    }
    .package_vipfunc_text {
      color: #282828;
      font-size: 14px;
      flex-grow: 1;
    }
    .package_vipfunc_span {
      width: 64px;
      height: 26px;
      background: linear-gradient(
        146deg,
        rgba(251, 210, 73, 1),
        rgba(247, 180, 33, 1),
        rgba(245, 166, 35, 1)
      );
      text-align: center;
      border-radius: 13px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      line-height: 26px;
    }
  }
}
</style>



